.titleBar{
  background-color: black;
  display:block;
  color:white;
  padding-left: 16px;
}

.inputBar{
  font-size: 24;
  display: block;
  width: 99%;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 16px;
}



.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
